import {
  Box,
  createTheme,
  ThemeProvider,
  useMediaQuery
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Navigate, Route, BrowserRouter as Router, Routes, useLocation } from 'react-router-dom';
import { Navigation } from './components/Navigation';
import { User } from './model';
import { Home } from './pages/Home';
import { Intro } from './pages/Intro';
import { Relations } from './pages/Relations';
import { Taxes } from './pages/Taxes';
import { Wealth } from './pages/Wealth';
import { Assistant } from './pages/Assistant';
import { fetchUser } from './data';

const theme = createTheme({
  palette: {
    primary: {
      main: '#FF0402',
    },
    secondary: {
      main: '#FF8C00', // Changed to a warm orange that complements the primary red
    },
    background: {
      default: '#F9FAFB',
      paper: '#FFFFFF',
    },
    text: {
      primary: '#111827',
      secondary: '#6B7280',
    },
  },
  typography: {
    fontFamily: 'SF Pro Display, Arial, sans-serif',
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
          border: '1px solid #E5E7EB',
          borderRadius: 4,
          padding: 16,
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 4,
          textTransform: 'none',
          fontWeight: 600,
          boxShadow: 'none',
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            borderRadius: 4,
          },
        },
      },
    },
    MuiAvatar: {
      styleOverrides: {
        root: {
          borderRadius: 4,
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
        },
      },
    },
  },
});

const AppContent: React.FC = () => {
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));
  const location = useLocation();
  const [user, setUser] = useState<User>(undefined);

  useEffect(() => {
    console.log(`Route changed to: ${location.pathname}`);
  }, [location]);

  useEffect(() => {
    console.log("Fetching data")
    const fetchData = async () => {
      try {
        const user : User = await fetchUser()
        setUser(user)
      } catch (error) {
        console.error(error)
      } finally {
      }
    };

    fetchData();
  }, []);

  return (
    <Box sx={{ display: 'flex', bgcolor: 'background.default', minHeight: '100vh' }}>
      {isDesktop && location.pathname !== '/' && <Navigation />}
      <Box sx={{ 
        flexGrow: 1, 
        pb: { xs: 7, md: 0 } // Add padding bottom on mobile
      }}>
        <Routes>
          <Route path="/" element={<Intro />} />
          <Route path="/home" element={<Home user={user}/>} />
          <Route path="/assistant" element={<Assistant user={user}/>} />
          <Route path="/relations" element={<Relations user={user} />} /> 
          <Route path="/wealth" element={<Wealth user={user}/>} />
          <Route path="/taxes" element={<Taxes user={user}/>} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Box>
      {!isDesktop && location.pathname !== '/' && <Navigation />}
    </Box>
  );
};

export const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <AppContent />
      </Router>
    </ThemeProvider>
  );
};