import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import SearchIcon from '@mui/icons-material/Search';
import ShowChartIcon from '@mui/icons-material/ShowChart';

import {
  Avatar,
  Box,
  Card,
  Collapse,
  Divider,
  Grid,
  IconButton,
  InputAdornment,
  List,
  ListItem,
  TextField,
  Typography
} from '@mui/material';
import { useEffect, useState } from 'react';
import { Taxable, TaxableSubType, TaxableSubTypeDescriptions, TaxableType, User } from '../model';

export const Wealth = ({ user }: { user: User }) => {
  const [expandedAssets, setExpandedAssets] = useState<Set<string>>(new Set());
  const [expandedGroups, setExpandedGroups] = useState<Set<string>>(new Set());
  const [searchTerm, setSearchTerm] = useState(''); 

  const groupTaxablesBySubTypeAndSum = (): { subType: TaxableSubType; totalAmountNet: number }[] => {
    const groupedTaxables = user?.taxables
    .filter(t => t.taxYear === 2022)
    .filter(t => t.type === TaxableType.WEALTH)
    .reduce((acc, t) => {
      acc[t.subType] = (acc[t.subType] || 0) + t.amountNet;
      return acc;
    }, {} as { [key in TaxableSubType]?: number }) || [];

    return Object.entries(groupedTaxables)
      .map(([subType, totalAmountNet]) => ({
        subType: subType as TaxableSubType,
        totalAmountNet: totalAmountNet
      }))
      .sort((a: any, b: any) => b.totalAmountNet - a.totalAmountNet);
  }

  const getIconForSubType = (subType: TaxableSubType) => {
    switch (subType) {
      case TaxableSubType.WEALTH_CASH_VALUE:
        return <AccountBalanceIcon />;
      case TaxableSubType.WEALTH_PORTFOLIO:
      case TaxableSubType.WEALTH_ASSET_VALUE:
      case TaxableSubType.WEALTH_CRYPTO_VALUE:
      case TaxableSubType.WEALTH_PE_VALUE:
        return <ShowChartIcon />;
      default:
        return <AccountBalanceIcon />;
    }
  };

  const getColorForSubType = (subType: TaxableSubType) => {
    switch (subType) {
      case TaxableSubType.WEALTH_CASH_VALUE:
        return '#FF0402'; // Primary color
      case TaxableSubType.WEALTH_PORTFOLIO:
        return '#66BB6A'; // Light green
      case TaxableSubType.WEALTH_ASSET_VALUE:
        return '#4CAF50'; // Medium green
      case TaxableSubType.WEALTH_CRYPTO_VALUE:
        return '#43A047'; // Dark green
      case TaxableSubType.WEALTH_PE_VALUE:
        return '#2E7D32'; // Darker green
      default:
        return '#C8E6C9'; // Very light green for any other cases
    }
  };

  const assetData = groupTaxablesBySubTypeAndSum()
    .map(item => ({
      subType: item.subType,
      value: item.totalAmountNet,
      icon: getIconForSubType(item.subType),
      color: getColorForSubType(item.subType),
    }));

  const totalAssets = assetData.reduce((sum, asset) => sum + asset.value, 0);

  useEffect(() => {
    if (searchTerm) {
      const matchingGroups = new Set<string>();
      const matchingAssets = new Set<string>();

      user?.taxables.forEach(t => {
        if (t.other.fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            t.self.fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            t.reference?.references[0].value.toLowerCase().includes(searchTerm.toLowerCase())) {
          matchingGroups.add(t.other.fullName);
          matchingAssets.add(t.subType);
        }
      });

      setExpandedGroups(matchingGroups);
      setExpandedAssets(matchingAssets);
    } else {
      setExpandedGroups(new Set());
      setExpandedAssets(new Set());
    }
  }, [searchTerm, user?.taxables]);

  return (
    <Box sx={{ p: 2, bgcolor: 'background.default' }}>
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}><Box component="span" sx={{ color: 'primary.main' }}>You</Box> Wealth</Typography>
      <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">
        {totalAssets.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
      </Typography>

      <TextField
        fullWidth
        variant="outlined"
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchIcon color="action" />
            </InputAdornment>
          ),
        }}
        sx={{
          mb: 3,
          bgcolor: 'background.paper',
          '& .MuiOutlinedInput-root': {
            '& fieldset': { borderColor: '#E5E7EB' },
            '&:hover fieldset': { borderColor: '#E5E7EB' },
            '&.Mui-focused fieldset': { borderColor: 'primary.main' },
          },
        }}
      />

      <Box sx={{ mb: 3, display: 'flex', height: 10, borderRadius: 2, overflow: 'hidden' }}>
        {assetData.map((asset, index) => (
          <Box
            key={index}
            sx={{
              height: '100%',
              bgcolor: asset.color,
              width: `${(asset.value / totalAssets) * 100}%`,
              transition: 'width 0.3s ease-in-out',
            }}
          />
        ))}
      </Box>

      {assetData.map((asset, index) => {
        const subAssets = user?.taxables?.filter(t => t.subType === asset.subType) || [];
        const groupedByOther = subAssets.reduce((acc, subAsset) => {
          const key = subAsset.other.fullName;
          if (!acc[key]) acc[key] = [];
          acc[key].push(subAsset);
          return acc;
        }, {} as { [key: string]: Taxable[] });

        const filteredGroups = Object.entries(groupedByOther).filter(([otherName, subAssets]) =>
          otherName.toLowerCase().includes(searchTerm.toLowerCase()) && subAssets.length > 0
        );

        if (filteredGroups.length === 0) return null;

        return (
          <Card key={index} sx={{ mb: 3, bgcolor: 'background.paper', overflow: 'hidden', p: 0 }}>
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                p: 2,
                cursor: 'pointer',
                '&:hover': { bgcolor: 'action.hover' },
              }}
              onClick={() => {
                const newExpandedAssets = new Set(expandedAssets);
                if (newExpandedAssets.has(asset.subType)) {
                  newExpandedAssets.delete(asset.subType);
                } else {
                  newExpandedAssets.add(asset.subType);
                }
                setExpandedAssets(newExpandedAssets);
              }}
            >
              <Avatar sx={{ bgcolor: asset.color, color: 'white', mr: 2 }}>{asset.icon}</Avatar>
              <Box sx={{ flexGrow: 1 }}>
                <Typography variant="subtitle1" fontWeight="medium">{TaxableSubTypeDescriptions[asset.subType]}</Typography>
                <Typography variant="body2" color="text.secondary">
                  {((asset.value / totalAssets) * 100).toFixed(1)}% of total
                </Typography>
              </Box>
              <Typography variant="subtitle1" fontWeight="bold" sx={{ mr: 2 }}>
                {asset.value.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
              </Typography>
              <IconButton size="small">
                {expandedAssets.has(asset.subType) ? <ExpandLessIcon /> : <ExpandMoreIcon />}
              </IconButton>
            </Box>
            <Collapse in={expandedAssets.has(asset.subType) || searchTerm !== ''}>
              <Divider />
              <Box sx={{ p: 2 }}>
                <Box sx={{
                  height: 6,
                  bgcolor: 'action.hover',
                  borderRadius: 3,
                  overflow: 'hidden',
                  mb: 2
                }}>
                  <Box sx={{
                    width: `${(asset.value / totalAssets) * 100}%`,
                    height: '100%',
                    bgcolor: asset.color,
                    transition: 'width 0.3s ease-in-out',
                  }} />
                </Box>
                {filteredGroups.map(([otherName, subAssets], subIndex) => (
                  <Box key={subIndex} sx={{ mb: subIndex < filteredGroups.length - 1 ? 2 : 0 }}>
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        p: 1.5,
                        borderRadius: 1,
                        cursor: 'pointer',
                        '&:hover': { bgcolor: 'action.hover' },
                      }}
                      onClick={() => {
                        const newExpandedGroups = new Set(expandedGroups);
                        if (newExpandedGroups.has(otherName)) {
                          newExpandedGroups.delete(otherName);
                        } else {
                          newExpandedGroups.add(otherName);
                        }
                        setExpandedGroups(newExpandedGroups);
                      }}
                    >
                      <Typography variant="body1" fontWeight="medium">{otherName}</Typography>
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Typography variant="body1" fontWeight="bold" sx={{ mr: 2 }}>
                          {subAssets.reduce((sum, subAsset) => sum + subAsset.amountNet, 0).toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
                        </Typography>
                        <IconButton size="small">
                          {expandedGroups.has(otherName) ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                        </IconButton>
                      </Box>
                    </Box>
                    <Collapse in={expandedGroups.has(otherName)}>
                      <List dense disablePadding sx={{ mt: 1, bgcolor: 'action.hover', borderRadius: 1 }}>
                        {subAssets.map((subAsset, subAssetIndex) => (
                          <ListItem 
                            key={subAssetIndex} 
                            sx={{ 
                              py: 1.5,
                              px: 2,
                              borderBottom: subAssetIndex < subAssets.length - 1 ? '1px solid rgba(0, 0, 0, 0.12)' : 'none',
                            }}
                          >
                            <Grid container alignItems="center" spacing={2}>
                              <Grid item xs={8}>
                                <Typography variant="body2"> 
                                {subAsset.self.fullName} {subAsset.reference ? "- " + subAsset.reference?.references[0].value : ''}
                                </Typography>
                              </Grid>
                              <Grid item xs={4}>
                                <Typography variant="body2" align="right" fontWeight="medium">
                                  {subAsset.amountNet.toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}
                                </Typography>
                              </Grid>
                            </Grid>
                          </ListItem>
                        ))}
                      </List>
                    </Collapse>
                  </Box>
                ))}
              </Box>
            </Collapse>
          </Card>
        );
      })}
    </Box>
  );
};